.nav{width:252px;height:100%;display:flex;flex-direction:column;justify-content:space-between}.nav__logo{display:block;width:133px;height:58px;margin-bottom:60px}.nav__logo img{width:100%;height:100%}.nav__list{padding:0}.nav__item{list-style:none;padding-bottom:8px}.nav__item:nth-child(6){position:relative}.nav__item:nth-child(6)::after{content:"Gói dịch vụ";width:100%;position:absolute;bottom:-75%;left:80%;transform:translate(-50%, -50%);font-weight:500;font-size:18px;line-height:26px;color:#1e0d03;letter-spacing:1.5%}.nav__link{padding:15px 0;text-decoration:none;display:flex;align-items:center;color:#1e0d03;transition:all .1s}.nav__link img{padding:0 15px 0 27px}.nav__link span{font-weight:500;font-size:18px;line-height:26px;color:#1e0d03;letter-spacing:1.5%}.nav__link img:nth-child(2){display:none}.nav__link img:nth-child(1){display:block}.active{background:#ffb800;border-radius:8px}.active span{font-weight:700;font-size:18px;line-height:26px;color:#fff}.active img:nth-child(1){display:none}.active img:nth-child(2){display:block}