.not-found{
        width: 100%;
        height: 100%;   
        position: relative;
    img{
        width: 100%;
        height: 100%;
        position: absolute;
        top:0;
        left:0;
        z-index: 1;
        border-radius: 12px;
    }
    & a{
        position: absolute;
        z-index: 9999;
        top:69%;
        left:47%;
        text-decoration: none;
        font-weight: 700;
        padding:10px 12px;
        color: #1E0D03;
        border-radius: 12px;
        background-color: orange;
    }
}